$global-header-background-color: $indigo-700;
$page-header-background-color: $indigo-500;
$page-header-background-color-hover: $indigo-50;

.global-header {
  background-color: $global-header-background-color;
  color: #fff;
  display: none;
}

.global-header.is-active {
  display: block;
}

.global-header--close {
  position: absolute;
  top: 1.3rem;
  right: 1rem;
  cursor: pointer;
}

.global-header--wrapper {
  padding-top: 1rem;
  padding-bottom: 1rem;
}

.global-header--title {
  font-size: 1.5rem;
  margin-bottom: 0.5rem;
  font-weight: 700;
}

.global-header--apps ul li + li {
  margin-top: 0.25rem;
}

.global-header--apps ul li a {
  font-weight: 700;
  color: #fff;
  text-decoration: none;
}

@media screen and (min-width: 1024px) {
  .global-header--wrapper {
    display: flex;
    align-items: center;
  }

  .global-header--title {
    margin-right: 3rem;
    margin-bottom: 0;
  }

  .global-header--apps ul {
    display: flex;
  }

  .global-header--apps ul li {
    margin-right: 2rem;
  }

  .global-header--apps ul li + li {
    margin-top: 0;
  }
}

.page-header {
  background-color: $page-header-background-color; /* page-header-bg-color */
  color: #fff; /* page-header-font-color */
  font-size: 1rem; /* page-header-font-size */
}

.page-header--wrapper {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  line-height: 1;
}

.page-header--apps-icon {
  width: 20px;
  height: 20px;
  text-align: center;
  cursor: pointer;
  padding-top: 2px;
}

.page-header--apps-icon.is-active {
  background-color: #fff;
  color: #3273dc;
}

.page-header--title {
  margin-left: 10px;
  font-weight: 700;
  font-size: 1.3rem;
}

.page-header--title a {
  text-decoration: none;
  color: #fff;
}

.page-header--menu.is-main-menu {
  order: 4;
  width: 100%;
}

.page-header--menu.is-user-menu {
  margin-left: auto;
}

.page-header--menu > ul {
  display: flex;
  overflow-x: auto;
  white-space: nowrap;
}

.page-header--menu > ul > li {
  margin-right: 2rem;
  border-bottom: 4px solid $page-header-background-color;
  padding: 14px 0 10px 0;
  cursor: pointer;
  height: 48px;
}

.page-header--menu > ul > li:last-child {
  margin-right: 0px;
}

.page-header--menu > ul > li.is-active, .page-header--menu > ul > li.is-selected, .page-header--menu > ul > li:hover {
  border-bottom: 4px solid $page-header-background-color-hover;
}

.page-header--menu > ul > li > a {
  color: #fff;
  text-decoration: none;
  display: block;
  line-height: 20px;
}

.page-header--menu > ul > li > a > img, .page-header--menu > ul > li > a > span {
  display: inline-block;
  vertical-align: middle;
  line-height: 1;
}

.page-header--menu > ul > li > a > img {
  line-height: 1;
}

.page-header--submenu {
  position: absolute;
  display: none;
  z-index: 1;
  background: #fff;
  width: 100%;
  height: 100px;
  top: 82px;
  left: 0;
  min-height: 20px;
  box-shadow: 0 3px 12px rgba(27,31,35,0.15);
  cursor: auto;
}

.page-header--menu > ul > li.has-submenu.is-active > div.page-header--submenu,
.page-header--menu > ul > li.has-submenu.is-hoverable:hover > div.page-header--submenu {
  display: block;
}

@media screen and (min-width: 1024px) {
  .page-header--menu.is-main-menu {
    order: unset;
    width: auto;
    margin-left: 4rem;
  }

  .page-header--menu > ul {
    overflow-x: unset;
  }

  .page-header--menu > ul > li {
    position: relative;
  }

  .page-header--menu > ul > li > div.page-header--submenu {
    left: 0;
    width: 300px;
  }

  .page-header--menu.is-user-menu .page-header--submenu {
    right: 0;
    left: unset;
  }

  .page-header--submenu {
    top: 42px;
  }
}
